import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/curriculum',
    name: 'curriculumView',
    component: () => import('@/views/curriculum.vue')
  },
  {
    path: '/control_casa',
    name: 'ccView',
    component: () => import('@/views/CCView.vue')
  },
  {
    path: '/contacto',
    name: 'contactoView',
    component: () => import('@/views/ContactoView.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
